import React, { Component } from "react";
import Image from "react-bootstrap/Image";

class WhtasappIcon extends Component {

  render() {
    return (
      <a
        href={this.props.link}
        target="_blank"
        rel="noopener noreferrer"
        className="btn rounded-circle btn-back-to-top"
        data-smooth-scroll
        data-aos="fade-up"
        data-aos-offset="0"
        data-aos-mirror="true"
        data-aos-once="false"
        style={{ marginBottom: 60 }}
      >
        <Image src="https://img.icons8.com/color/58/000000/whatsapp.png" />
      </a>
    )
  }
}

export default WhtasappIcon
