import React, { Component } from "react";
import { Button } from "react-bootstrap";
import Article from "./Article";
import ReactPlayer from "react-player";
var dataContent = [];
var viewVidio = "";
var tampilVidio = "";
let noVidio = 0;
class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tampilButton: false,
      tampilButtonPlus: false,
      tempData: false
    };
  }
  handlingCheck = dataEvent => {
    if (this.state.tempData === false) {
      const id_list_materi = localStorage.getItem("SubContent");

      dataContent = dataEvent.filter(item => item._id === id_list_materi);

      viewVidio = dataContent ? dataContent[0] : [];
      tampilVidio = viewVidio ? viewVidio.id_youtube : "";
    }
  };

  componentDidUpdate(nextProps) {
    if (nextProps.data.length) {
      if (this.state.tempData === false) {
        this.setState({ tempData: true });
      }
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const id_list_materi = localStorage.getItem("SubContent");
    const backData = nextProps.data;
    let dataBack = backData.filter(item => item._id === id_list_materi);
    let tempVidio = dataBack[0] ? dataBack[0].id_list : 0;
    noVidio = tempVidio;
    if (tempVidio === 5) {
      this.setState({
        tampilButton: true,
        tempData: false,
        tampilButtonPlus: false
      });
    } else {
      this.setState({
        tampilButton: false,
        tempData: false,
        tampilButtonPlus: false
      });
    }

    if (tempVidio / 5 === backData.length) {
      this.setState({ tampilButtonPlus: true });
    } else {
      this.setState({ tampilButtonPlus: false });
    }
  }

  handlingVidio = (e, operator, key) => {
    const { data } = this.props;
    e.preventDefault();
    localStorage.setItem("SubContent", key);

    let tempVidio = viewVidio ? viewVidio.id_list : 0;

    if (operator === "-") {
      noVidio = tempVidio === 5 ? 5 : tempVidio - 5;
    }
    if (operator === "+") {
      noVidio = tempVidio + 5;
    }
    if (noVidio === 5) {
      this.setState({ tampilButton: true, tampilButtonPlus: false });
    } else {
      this.setState({ tampilButton: false, tampilButtonPlus: false });
    }

    if (noVidio / 5 === data.length) {
      this.setState({ tampilButtonPlus: true });
    } else {
      this.setState({ tampilButtonPlus: false });
    }

    dataContent = data.filter(item => item.id_list === noVidio);
    viewVidio = dataContent ? dataContent[0] : [];
    tampilVidio = viewVidio ? viewVidio.id_youtube : "";
    this.setState({ tempData: true });
  };

  render() {
    const { data } = this.props;
    const checkData = data ? data : [];
    this.handlingCheck(checkData);

    return (
      <section className="container" id={viewVidio ? viewVidio.title : ""}>
        <span
          style={{
            marginBottom: 20,
            paddingLeft: 20,
            color: "black",
            fontSize: 20
          }}
        >
          {viewVidio ? viewVidio.title : ""}
        </span>
        {tampilVidio === "-" ? (
          <span id={viewVidio ? viewVidio.title : ""}> </span>
        ) : (
            <div
              style={{ marginLeft: 20, marginRight: 20 }}
              className="player-wrapper"
              id={viewVidio ? viewVidio.title : ""}
            >
              <ReactPlayer
                className="react-player"
                width="auto"
                height={450}
                controls
                light={viewVidio ? viewVidio.image_youtube : ""}
                playing
                config={{
                  youtube: {
                    playerVars: { showinfo: 1, encryptedMedia: "gyroscope" }
                  }
                }}
                url={
                  viewVidio
                    ? "https://www.youtube.com/watch?v=" + viewVidio.id_youtube
                    : ""
                }
              />
            </div>
          )}
        <div className="row container" style={{ position: "relative", top: 40 }}>
          <div className="col-md-4">
            <span
              className="responsiveText"
              style={{
                position: "absolute",
                top: tampilVidio === "-" ? -30 : -30,
                left: 20,
                float: "left"
              }}
            >
              Materi No : {noVidio / 5}
            </span>
          </div>
          <div className="col-md-8 text-right">
            <div
              style={{
                position: "absolute",
                top: -30,
                right: 0,
                float: "right"
              }}
            >
              <Button
                variant="outline-primary"
                style={{ marginRight: 10, color: "black", fontSize: 14 }}
                onClick={e => {
                  this.handlingVidio(e, "-", viewVidio ? viewVidio._id : 0);
                }}
                disabled={this.state.tampilButton}
              >
                Materi Sebelumnya
              </Button>

              <Button
                style={{ color: "black", fontSize: 14 }}
                variant="outline-primary"
                onClick={e => {
                  this.handlingVidio(e, "+", viewVidio ? viewVidio._id : 0);
                }}
                disabled={this.state.tampilButtonPlus}
              >
                Materi Salanjutnya
              </Button>
            </div>
          </div>
        </div>
        <div style={{ marginTop: 60 }}>
          <Article data={viewVidio} />
        </div>
      </section>
    );
  }
}

export default Content;
