import request from "superagent";
import { responseContent, responseContentSelected } from "./index";
import errorHandling from "../exceptions/errorapi/Index";

export const requestContent = token => {
  return dispatch => {
    try {
      request
        .post(`${process.env.REACT_APP_IP}/elearning/getallmateri`)
        .set("Content-Type", "application/json")
        .send({ token: token })
        .end((err, res) => {
          if (
            res.body.code === 1000 ||
            res.body.code === 2000 ||
            res.body.code === 9000
          ) {
            dispatch(responseContent(res.body));
          } else {
            dispatch(responseContent(errorHandling.exceptions.tokenExpired));
          }
        });
    } catch (err) {
      dispatch(responseContent(errorHandling.exceptions.tokenExpired));
    }
  };
};

export const requestContentSelected = (setMateri, setContent) => {
  return dispatch => {
    if (typeof setMateri === "number" && typeof setContent === "number") {
      dispatch(
        responseContentSelected({
          setMateri: setMateri,
          setContent: setContent
        })
      );
    } else {
      dispatch(responseContentSelected({ setContent: setContent }));
    }
  };
};
