import React, { Component } from "react";
import { connect } from "react-redux";
import { requestGoogleSistemBelajar } from "../../actions/google-sheet";
var dataContent = [];

class SistemBelajar extends Component {
  componentDidMount() {
    this.props.requestGoogleSistemBelajar();
  }

  getContent = value => {
    if (value) {
      dataContent = value;
    }
  };

  render() {
    this.getContent(this.props.responseGoogleSistemBelajar);

    return (
      <div
        className="tab-pane fade"
        id="home"
        role="tabpanel"
        aria-labelledby="home-tab"
      >
        <div
          className="row justify-content-center"
          dataaos="fade-up"
          dataaossoffset="250"
        >
          {" "}
          <div className="col-xl-10 col-lg-11">
            {dataContent.map((data, key) => {
              return (
                <div className="mb-3 mb-sm-4" key={key}>
                  <div className="card card-body flex-md-row align-items-start align-items-md-center hover-box-shadow">
                    <img src={data[0]} alt="Icon" className="icon icon-lg" />
                    <div className="mt-3 mt-sm-4 mt-md-0 ml-md-5 ml-lg-4 ml-xl-5">
                      <h4>{data[1]}</h4>
                      <p>{data[2]}</p>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleSistemBelajar: state.responseGoogleSistemBelajar
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleSistemBelajar: () => dispatch(requestGoogleSistemBelajar())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SistemBelajar);
