import React, { Component } from "react";
import Headers from "../Headers";
import Footers from "../Footers";
import swal from "sweetalert";
import { override } from "./style_loading";

import PacmanLoader from "react-spinners/PacmanLoader";

class Index extends Component {
  constructor() {
    super();
    this.state = {
      dataForm: {
        name: "",
        email: "",
        phone_number: "",
        birth_date: "",
        sex: "",
        city: "",
        job: "",
        skill: "",
        tshirt_size: "",
        registrasion_code: "TOKPEDBELAJAR",
        device: navigator.userAgent
      },
      validate: true,
      loading: false
    };
  }

  inputChange = e => {
    let newdataForm = { ...this.state.dataForm };

    newdataForm[e.target.name] = e.target.value;
    this.setState(
      {
        dataForm: newdataForm
      },
      () => console.log(this.state.dataForm)
    );
  };

  bersih = () => {
    this.setState({
      dataForm: {
        name: "",
        email: "",
        phone_number: "",
        birth_date: "",
        sex: "",
        city: "",
        job: "",
        skill: "",
        tshirt_size: ""
      }
    });
  };

  handler_registration = () => {
    const { dataForm } = this.state;
    fetch(`${process.env.REACT_APP_IP_REGISTRATION}/registrasi`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(dataForm)
    }).then(e => {
      if (e.status === 200) {
        swal({
          title: "Selamat",
          text: "Anda Berhasil Registrasi",
          icon: "success",
          button: "Oke"
        });
        this.setState({
          loading: false
        });
        this.bersih();
      } else {
        swal({
          title: "Maaf ...!",
          text: "Anda Gagal Registrasi, Silahkan Ulangi...",
          icon: "warning",
          dangerMode: true
        });
      }
    });
  };

  validate = e => {
    e.preventDefault();
    let newdataForm = this.state.dataForm;
    if (this.validateEmail(newdataForm.email) === false) {
      swal({
        title: "Maaf ...!",
        text: "Email Anda Belum Terisi Dengan Benar",
        icon: "warning",
        dangerMode: true
      });
      this.validatesetState(false);
    } else if (newdataForm.name === "") {
      this.validatesetState(false);
    } else if (newdataForm.birth_date === "") {
      this.validatesetState(false);
    } else if (newdataForm.sex === "") {
      this.validatesetState(false);
    } else if (newdataForm.phone_number === "") {
      this.validatesetState(false);
    } else if (newdataForm.city === "") {
      this.validatesetState(false);
    } else if (newdataForm.job === "") {
      this.validatesetState(false);
    } else if (newdataForm.skill === "") {
      this.validatesetState(false);
    } else if (newdataForm.tshirt_size === "") {
      this.validatesetState(false);
    } else if (newdataForm.registrasion_code === "") {
      this.validatesetState(false);
    } else {
      this.validatesetState(true);
      this.setState({ loading: true });
      this.handler_registration();
    }
  };

  validatesetState = value => {
    this.setState({
      validate: value
    });
  };

  validateEmail = email => {
    const expression = /(?!.*\.{2})^([a-z\d!#$%&'*+\-=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+(\.[a-z\d!#$%&'*+\-=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+)*|"((([\t]*\r\n)?[\t]+)?())*(([\t]*\r\n)?[\t]+)?")@(([a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.)+([a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.?$/i;

    return expression.test(String(email).toLowerCase());
  };

  render() {
    return (
      <div>
        <Headers />
        <section id="apply" className="bg-light pb-0">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-xl-6 col-lg-6 col-md-7">
                <div className="card card-body shadow">
                  <form data-form-email noValidate action="/">
                    <div className="form-group">
                      <label htmlFor="demo-email">Alamat Email</label>
                      <input
                        id="demo-email"
                        name="email"
                        type="email"
                        className="form-control"
                        placeholder="you@demo.com"
                        value={this.state.dataForm.email}
                        onChange={e => this.inputChange(e)}
                        required
                      />
                      <div className="invalid-feedback">Email Anda.</div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="demo-name">Nama Lengkap</label>
                      <input
                        id="demo-name"
                        name="name"
                        type="text"
                        value={this.state.dataForm.name}
                        onChange={e => this.inputChange(e)}
                        className="form-control"
                        placeholder="Jawaban Anda"
                        required
                      />
                      <div className="invalid-feedback">Jawaban Anda.</div>
                    </div>
                    <div className="form-group ">
                      <label htmlFor="tanggal">Tanggal Lahir</label>
                      <input
                        id="tanggal"
                        name="birth_date"
                        type="date"
                        onChange={e => this.inputChange(e)}
                        value={this.state.dataForm.birth_date}
                        className="form-control col-sm-6 "
                        placeholder="Type here"
                        required
                      />
                      <div className="invalid-feedback">Tanggal.</div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="jenis-kelamin">Jenis Kelamin</label>
                      <div className="position-relative">
                        <select
                          name="sex"
                          className="custom-select"
                          id="jenis-kelamin"
                          required
                          onChange={e => {
                            this.inputChange(e);
                          }}
                        >
                          <option value="">Pilih</option>
                          <option value="M">Laki - Laki</option>
                          <option value="F">Perempuan</option>
                        </select>
                        <img
                          src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-caret-down.svg"
                          alt="Arrow Down"
                          className="icon icon-sm"
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="handphone" style={{ width: "100%" }}>
                        No Handphone WhatsApp
                      </label>

                      <span style={{ fontSize: 12 }}>
                        No HP yang terhubung dengan WA. Contoh Penulisan :
                        085216601682
                      </span>
                      <input
                        onChange={e => this.inputChange(e)}
                        value={this.state.dataForm.phone_number}
                        id="handphone"
                        name="phone_number"
                        type="number"
                        className="form-control"
                        placeholder="Jawaban Anda"
                        required
                      />
                      <div className="invalid-feedback">Jawaban Anda.</div>
                    </div>
                    <div className="form-group ">
                      <label htmlFor="domisili" style={{ width: "100%" }}>
                        Kota / Kabupaten DOMISILI
                      </label>

                      <span style={{ fontSize: 12 }}>
                        Masukkan Nama Kota / Kabupaten yang kamu tinggali saat
                        ini
                      </span>
                      <input
                        id="domisili"
                        name="city"
                        type="text"
                        onChange={e => this.inputChange(e)}
                        value={this.state.dataForm.city}
                        className="form-control col-sm-8"
                        placeholder="Jawaban Anda"
                        required
                      />
                      <div className="invalid-feedback">Jawaban Anda.</div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="status-pekerjaan">Status Pekerjaan</label>
                      <div className="position-relative">
                        <select
                          name="job"
                          className="custom-select"
                          id="status-pekerjaan"
                          required
                          onChange={e => {
                            this.inputChange(e);
                          }}
                        >
                          <option value="">Pilih</option>
                          <option value="Masih Sekolah (SMA / SMP)">
                            Masih Sekolah (SMA / SMP)
                          </option>
                          <option value="Mahasiswa">Mahasiswa</option>
                          <option value="Fresh Graduate">Fresh Graduate</option>
                          <option value="Freelancer">Freelancer</option>
                          <option value="Wiraswasta (Usaha Sendiri)">
                            Wiraswasta (Usaha Sendiri)
                          </option>
                          <option value="Karyawan (Swasta / PNS)">
                            Karyawan (Swasta / PNS)
                          </option>
                          <option value="Guru">Guru</option>
                          <option value="Belum Bekerja">Belum Bekerja</option>
                          <option value="Lainnya">Lainnya</option>
                        </select>
                        <img
                          src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-caret-down.svg"
                          alt="Arrow Down"
                          className="icon icon-sm"
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="belajar-programming">
                        Pernah Belajar Programming ?
                      </label>
                      <div className="position-relative">
                        <select
                          className="custom-select"
                          id="belajar-programming"
                          name="skill"
                          required
                          onChange={e => {
                            this.inputChange(e);
                          }}
                        >
                          <option value="">Pilih</option>
                          <option value="1">Belum Pernah</option>
                          <option value="2">
                            Sudah Pernah (Hanya Coba-Coba) Dan Masih Dasar
                          </option>
                          <option value="3">
                            Sudah Pernah Dan Sudah Cukup Paham
                          </option>
                        </select>
                        <img
                          src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-caret-down.svg"
                          alt="Arrow Down"
                          className="icon icon-sm"
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="ukuran-kaos" style={{ width: "100%" }}>
                        Ukuran Kaos (GRATIS KAOS NIOMIC, TERBATAS )
                      </label>
                      <span style={{ fontSize: 12 }}>
                        Segera Konfirmasi Pembayaran Untuk Mendapatkan KAOS
                        NIOMIC
                      </span>
                      <div className="position-relative">
                        <select
                          className="custom-select"
                          id="ukuran-kaos"
                          required
                          name="tshirt_size"
                          onChange={e => {
                            this.inputChange(e);
                          }}
                        >
                          <option value="">Pilih</option>
                          <option value="S">S</option>
                          <option value="M">M</option>
                          <option value="L">L</option>
                          <option value="XL">XL</option>
                          <option value="XXL">XXL</option>
                          <option value="XXXL">XXXL</option>
                          <option value="XXXXL">XXXXL</option>
                        </select>
                        <img
                          src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-caret-down.svg"
                          alt="Arrow Down"
                          className="icon icon-sm"
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="promo-code" style={{ width: "100%" }}>
                        Promo Code
                      </label>

                      <span style={{ fontSize: 12 }}>
                        JANGAN HAPUS / DIGANTI, Kode Promo Otomatis Terisi
                      </span>
                      <input
                        onChange={e => this.inputChange(e)}
                        value={this.state.dataForm.registrasion_code}
                        id="promo-code"
                        name="registrasion_code"
                        type="text"
                        className="form-control"
                        placeholder="Jawaban Anda"
                        required
                      />
                      <div className="invalid-feedback">Jawaban Anda.</div>
                    </div>
                    <div
                      className="d-none alert alert-success w-100 my-md-3"
                      role="alert"
                      data-success-message
                    ></div>

                    <div
                      role="alert"
                      data-error-message
                      style={{ opacity: 0 }}
                    ></div>

                    {this.state.validate === false ? (
                      <div className="alert alert-danger w-100 my-md-3">
                        Silahkan Lengkapi Data Anda
                      </div>
                    ) : (
                      <div> </div>
                    )}

                    <div style={{ marginBottom: 30 }}>
                      <PacmanLoader
                        css={override}
                        size={25}
                        //size={"150px"} this also works
                        color={"#123abc"}
                        loading={this.state.loading}
                      />
                    </div>

                    <button
                      className="btn btn-primary btn-block btn-loading"
                      type="submit"
                      onClick={e => this.validate(e)}
                    >
                      <span>Kirim</span>
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <br />
        </section>
        <Footers />
      </div>
    );
  }
}

export default Index;
