import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { requestContent } from "../../actions/content";
import Headers from "../HeaderCourses";
import Slider from "./Slider";
import SubContent from "./SubContent";
import Content from "./Content";
import Footers from "../Footers";
var materi = [];
class Index extends Component {
  UNSAFE_componentWillMount() {
    const token = sessionStorage.getItem("browser_default") || "";
    const name = sessionStorage.getItem("browser_name") || "";
    if (token && name) {
      this.props.requestContent(token);
    } else if (
      !token ||
      !name ||
      (this.props.responseContent.hasOwnProperty("code") &&
        this.props.responseContent.code !== 1000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { responseContent } = nextProps;
    if (
      responseContent.hasOwnProperty("code") &&
      (responseContent.code === 2000 || responseContent.code === 9000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }

  handlingLogout() {
    sessionStorage.removeItem("browser_default");
    sessionStorage.removeItem("browser_name");
  }
  render() {
    const { responseContent } = this.props;
    responseContent ? (materi = responseContent.message) : (materi = []);

    return (
      <div>
        <Headers listMateri={false} />

        <div className="divider divider-bottom bg-white"></div>
        <Slider />
        <div className="row">
          <div className="col-md-3">
            <SubContent data={materi} />
          </div>
          <div className="col-md-9">
            <Content dataMateri={materi} />
          </div>
        </div>
        <a
          href="#top"
          className="btn btn-primary rounded-circle btn-back-to-top"
          data-smooth-scroll
          data-aos="fade-up"
          data-aos-offset="500"
          data-aos-mirror="true"
          data-aos-once="false"
        >
          <img
            src={"assets/img/icons/interface/icon-arrow-up.svg"}
            alt="Icon"
            className="icon "
            datainjectsvg="true"
          />
        </a>
        <Footers />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseContent: state.responseContent
  };
};

const mapDispatchToProps = dispatch => ({
  requestContent: token => dispatch(requestContent(token))
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Index)
);
