import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import LandingPage from "./component/landing-page/Index";
// import PageNotFound from "./exceptions/errorapi/Index";
import TestYoutube from "./component/youtube/Index";
import FormRegistration from "./component/form-registration/Index";
import SistemBelajar from "./component/sistem-belajar/Index";
import CaraPembayaran from "./component/cara-pembayaran/Index";
import BatchSebelumnya from "./component/landing-page/SectionBatchSebelumnya";
import Login from "./component/login/Index";
import ContentMateri from "./component/content-materi/Index";
import Materi from "./component/materi/Index";
import ListMateri from "./component/list-materi/Index";
//import "semantic-ui-css/semantic.min.css";

class Middleware extends Component {
  render() {
    return (
      <div>
        <Router>
          <Switch>
            <Route exact path="/" render={() => <LandingPage />} />
            <Route
              exact
              path="/registration"
              render={() => <FormRegistration />}
            />
            <Route exact path="/youtube" render={() => <TestYoutube />} />
            <Route
              exact
              path="/sistem-belajar"
              render={() => <SistemBelajar />}
            />
            <Route
              exact
              path="/cara-pembayaran"
              render={() => <CaraPembayaran />}
            />

            <Route
              exact
              path="/batch-sebelumnya"
              render={() => <BatchSebelumnya />}
            />

            <Route exact path="/login" render={() => <Login />} />
            <Route exact path="/courses" render={() => <ContentMateri />} />

            <Route exact path="/materi" render={() => <Materi />} />
            <Route exact path="/list-materi" render={() => <ListMateri />} />
            <Route exact path="*" render={() => <LandingPage />} />
          </Switch>
        </Router>
      </div>
    );
  }
}

export default Middleware;
