import React, { Component } from "react";

import { Image } from "react-bootstrap";
import { connect } from "react-redux";
import { requestGoogleSection } from "../../actions/google-sheet";
var dataContent = [];
var judul = [];
var subJudul = [];
var body = [];
var izin = [];
var url = [];
var image = [];
var imgapp = [];
class Section3 extends Component {
  componentDidMount() {
    this.props.requestGoogleSection();
  }
  getContent = () => {
    this.props.responseGoogleSection
      ? (dataContent = this.props.responseGoogleSection)
      : (dataContent = []);
    var path = "";

    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (path = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (path = "");
      if (path === "judul") {
        for (var a = 0; a < i; a++) {
          judul.push(dataContent[a]);
        }
      } else if (path === "subjudul") {
        for (var b = judul.length + 1; b < i; b++) {
          subJudul.push(dataContent[b]);
        }
      } else if (path === "body") {
        for (var c = subJudul.length + 1 + (judul.length + 1); c < i; c++) {
          body.push(dataContent[c]);
        }
      } else if (path === "izin") {
        for (
          var d = subJudul.length + 1 + (judul.length + 1) + (body.length + 1);
          d < i;
          d++
        ) {
          izin.push(dataContent[d]);
        }
      } else if (path === "url") {
        for (
          var e =
            subJudul.length +
            1 +
            (judul.length + 1) +
            (body.length + 1) +
            (izin.length + 1);
          e < i;
          e++
        ) {
          url.push(dataContent[e]);
        }
      } else if (path === "img") {
        for (
          var f =
            subJudul.length +
            1 +
            (judul.length + 1) +
            (body.length + 1) +
            (izin.length + 1) +
            (url.length + 1);
          f < i;
          f++
        ) {
          image.push(dataContent[f]);
        }
      } else if (path === "imgapp") {
        for (
          var g =
            subJudul.length +
            1 +
            (judul.length + 1) +
            (body.length + 1) +
            (izin.length + 1) +
            (url.length + 1) +
            (image.length + 1);
          g < i;
          g++
        ) {
          imgapp.push(dataContent[g]);
        }
      }
    }
  };
  render() {
    this.getContent();
    return (
      <section className="p-0">
        <div className="container">
          <div className="row align-items-center justify-content-around">
            <div className="col-md-9 col-lg-6 col-xl-5 mb-4 mb-md-5 mb-lg-0 pl-lg-5 pl-xl-0">
              <div className="text-center text-lg-left">
                <h2 className="h2">{judul}</h2>
                <p className="lead">
                  {subJudul[0]}
                  <a href={subJudul[2]}> {subJudul[1]}</a>.
                  <br />
                  <br />
                  {body.map((data, key) => {
                    return (
                      <span key={key}>
                        <br />
                        {data}
                      </span>
                    );
                  })}
                </p>

                <h6 style={{ marginTop: 60, color: "#009b72" }}>{izin[0]}</h6>
                <Image alt="Image" src={image[1]} />
              </div>
            </div>
            <div className="col-md-9 col-lg-5" data-aos="fade-in">
              <Image
                src={imgapp[0]}
                alt="Info"
                className="img-fluid rounded shadow"
              />
              <Image
                src={imgapp[1]}
                alt="Info"
                className="position-absolute p-0 col-4 col-xl-5 border border-white border-thick rounded-circle top right shadow-lg mt-5 mr-n5 mr-lg-n3 mr-xl-n5 d-none d-md-block"
                datajarallaxelement="-20 0"
              />
            </div>
          </div>
        </div>

        <div className="divider divider-bottom bg-white"></div>
      </section>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleSection: state.responseGoogleSection
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleSection: () => dispatch(requestGoogleSection())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Section3);
