import request from "superagent";
import {
  responseHomeGoogle,
  responseGoogleTeknologi,
  responseGoogleAlurPendaftaran,
  responseGoogleSection,
  responseGoogleBeforeFooter,
  responseGoogleQuote,
  responseGoogleBatchSebelumnya,
  responseGoogleFooter,
  responseGoogleSillabus,
  responseGoogleSistemBelajar,
  responseGoogleKeuntungan,
  responseGoogleCaraPembayaran,
  responseGoogleMetodePembayaran
} from "./index";
import errorHandling from "../exceptions/errorapi/Index";

export const requestHomeGoogle = () => {
  return dispatch => {
    try {
      request
        .get(
          `https://sheets.googleapis.com/v4/spreadsheets/${process.env.REACT_APP_SHEET_ID_GET}/values/Background!${process.env.REACT_APP_VALUES_SHEET}/?key=${process.env.REACT_APP_KEY_GOOGLE}`
        )
        .set("Content-Type", "application/json")
        .end((err, res) => {
          if (res) {
            if (res.status === 200) {
              dispatch(responseHomeGoogle(res.body.values));
            } else {
              dispatch(
                responseHomeGoogle(errorHandling.exceptions.errorGlobal)
              );
            }
          } else {
            dispatch(responseHomeGoogle(errorHandling.exceptions.errorGlobal));
          }
        });
    } catch (err) {
      dispatch(responseHomeGoogle(errorHandling.exceptions.errorGlobal));
    }
  };
};

