import React, { Component } from "react";
import MetodePembayaran from "./MetodePembayaran";
import Headers from "../Headers";
import { connect } from "react-redux";
import { requestGoogleCaraPembayaran } from "../../actions/google-sheet";

import Footers from "../Footers";

var dataContent = [];
var dataJudul = [];
var dataIsiHeader = [];
var dataIsiContent = [];
var dataNote = [];
class Index extends Component {
  componentDidMount() {
    this.props.requestGoogleCaraPembayaran();
  }

  getContent = () => {
    const { responseGoogleCaraPembayaran } = this.props;
    responseGoogleCaraPembayaran
      ? (dataContent = responseGoogleCaraPembayaran)
      : (dataContent = []);
    var header = "";
    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (header = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (header = "");
      if (header === "judul") {
        for (var a = 0; a < i; a++) {
          dataJudul.push(dataContent[a]);
        }
      } else if (header === "isiHeader") {
        for (var b = dataJudul.length + 1; b < i; b++) {
          dataIsiHeader.push(dataContent[b]);
        }
      } else if (header === "isiContent") {
        for (
          var c = dataJudul.length + 1 + (dataIsiHeader.length + 1);
          c < i;
          c++
        ) {
          dataIsiContent.push(dataContent[c]);
        }
      } else if (header === "note") {
        for (
          var d =
            dataJudul.length +
            1 +
            (dataIsiHeader.length + 1) +
            (dataIsiContent.length + 1);
          d < i;
          d++
        ) {
          dataNote.push(dataContent[d]);
        }
      }
    }
  };

  render() {
    this.getContent();
    return (
      <div>
        <Headers />
        <section className="bg-light o-hidden">
          <div className="container">
            <div className="text-white">
              <div className="row section-title justify-content-center text-center">
                <div className="col-md-9 col-lg-8 col-xl-7">
                  <h1 className="text-primary">{dataJudul}</h1>
                </div>
              </div>
            </div>
            <div className="row align-items-center justify-content-center">
              {dataIsiHeader.map((data, key) => {
                return (
                  <div
                    key={key}
                    className="col-sm-9 col-md-7 col-lg-3 mb-3 mb-md-4 mb-lg-0"
                    data-aos="fade-up"
                    data-aos-delay="100"
                  >
                    <div className="card card-body align-items-center shadow">
                      <div className="text-center mb-4">
                        <h4>{data[0]}</h4>
                        <div className="d-flex align-items-center justify-content-center mb-1">
                          <span className="h6 mb-0 mr-2">{data[1]}</span>
                          <span
                            className="h4 mb-0 text-dark"
                            style={{ textDecoration: "line-through" }}
                          >
                            {data[2]}
                          </span>
                        </div>
                        <div className="d-flex align-items-center justify-content-center mb-1">
                          <span className="h5 mb-0 mr-2">{data[3]}</span>
                          <span className="h2 mb-0 text-dark">{data[4]}</span>
                        </div>
                        <div>{data[5]}</div>
                      </div>
                      <ul
                        className="list-unstyled p-0"
                        style={{ marginLeft: -25, marginRight: -25 }}
                      >
                        {dataIsiContent[key].map((viewData, key2) => {
                          return (
                            <li className="my-3" key={key2}>
                              <div className="d-flex align-items-center">
                                <div className="rounded-circle bg-success-alt">
                                  <img
                                    src={data[6]}
                                    alt=" icon"
                                    className="m-2 icon icon-xs bg-success"
                                    datainjectsvg="true"
                                  />
                                </div>
                                <h6 className="mb-0 ml-3">{viewData}</h6>
                              </div>
                            </li>
                          );
                        })}
                      </ul>
                    </div>
                  </div>
                );
              })}
            </div>

            <p style={{ color: "#555A64", padding: 10, fontSize: "small" }}>
              <b>{dataNote[0]}</b> {dataNote[1]}
            </p>
          </div>
        </section>

        <MetodePembayaran />
        <Footers />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleCaraPembayaran: state.responseGoogleCaraPembayaran
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleCaraPembayaran: () => dispatch(requestGoogleCaraPembayaran())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Index);
