import React, { Component } from "react";
import Headers from "../HeaderCourses";
import Footers from "../Footers";
import Content from "./Content";
import SubMenu from "./SubMenu";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { requestContent } from "../../actions/content";

class Index extends Component {
  constructor() {
    super();
    this.state = {
      subMateri: []
    };
  }
  UNSAFE_componentWillMount() {
    const token = sessionStorage.getItem("browser_default") || "";
    const name = sessionStorage.getItem("browser_name") || "";
    if (token && name) {
      this.props.requestContent(token);
    } else if (
      !token ||
      !name ||
      (this.props.responseContent.hasOwnProperty("code") &&
        this.props.responseContent.code !== 1000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }
  handlingLogout() {
    sessionStorage.removeItem("browser_default");
    sessionStorage.removeItem("browser_name");
  }
  UNSAFE_componentWillReceiveProps(nextProps) {
    const { responseContent } = nextProps;
    const content = localStorage.getItem("content");
    let noContent = content ? content : 0;

    let tempMateri = responseContent ? responseContent.message : [];

    var filterMateri =
      noContent === 0
        ? tempMateri[0]
        : tempMateri.filter(item => item._id === noContent);

    this.setState({
      subMateri: filterMateri
        ? noContent === 0
          ? filterMateri.id_list_materi
          : filterMateri[0].id_list_materi
        : []
    });
    if (
      responseContent.hasOwnProperty("code") &&
      (responseContent.code === 2000 || responseContent.code === 9000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }

  render() {
    return (
      <div>
        <Headers />

        <div className="row">
          <div className="col-md-8">
            <Content data={this.state.subMateri} />
          </div>
          <div className="col-md-4">
            <div>
              <SubMenu data={this.state.subMateri} />
            </div>
          </div>
        </div>
        <Footers />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseContent: state.responseContent
  };
};

const mapDispatchToProps = dispatch => ({
  requestContent: token => dispatch(requestContent(token))
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Index)
);
