import { combineReducers } from "redux";
import { responseHome } from "./home.js";
import { responseContent, responseContentSelected } from "./content.js";
import { responseLogin } from "./login.js";
import {
  responseHomeGoogle,
  responseGoogleTeknologi,
  responseGoogleAlurPendaftaran,
  responseGoogleSection,
  responseGoogleBeforeFooter,
  responseGoogleQuote,
  responseGoogleBatchSebelumnya,
  responseGoogleFooter,
  responseGoogleSillabus,
  responseGoogleKeuntungan,
  responseGoogleSistemBelajar,
  responseGoogleCaraPembayaran,
  responseGoogleMetodePembayaran
} from "./google-sheet";

const rootReducers = combineReducers({
  responseHome: responseHome,
  responseHomeGoogle: responseHomeGoogle,
  responseGoogleTeknologi: responseGoogleTeknologi,
  responseGoogleAlurPendaftaran: responseGoogleAlurPendaftaran,
  responseGoogleSection: responseGoogleSection,
  responseGoogleBeforeFooter: responseGoogleBeforeFooter,
  responseGoogleQuote: responseGoogleQuote,
  responseGoogleBatchSebelumnya: responseGoogleBatchSebelumnya,
  responseGoogleFooter: responseGoogleFooter,
  responseGoogleSillabus: responseGoogleSillabus,
  responseGoogleSistemBelajar: responseGoogleSistemBelajar,
  responseGoogleKeuntungan: responseGoogleKeuntungan,
  responseGoogleCaraPembayaran: responseGoogleCaraPembayaran,
  responseGoogleMetodePembayaran: responseGoogleMetodePembayaran,
  responseLogin: responseLogin,
  responseContentSelected: responseContentSelected,
  responseContent: responseContent
});

export default rootReducers;
