import React, { Component } from "react";

import Home from "./SistemBelajar";
import Keuntungan from "./Keuntungan";
import Silabus from "./Silabus";
import Headers from "../Headers";
import Footers from "../Footers";
class Index extends Component {
  render() {
    return (
      <div>
        {this.props.index === "true" ? "" : <Headers />}

        <section className="bg-light o-hidden">
          <div className="container">
            <div className="row">
              <div className="col">
                <ul
                  className="nav nav-pills lead mb-4 mb-md-5 justify-content-center"
                  id="myTab"
                  role="tablist"
                >
                  <li className="nav-item">
                    <a
                      className="nav-link "
                      id="home-tab"
                      data-toggle="tab"
                      href="#home"
                      role="tab"
                      aria-controls="home"
                      aria-selected="false"
                    >
                      Sistem Belajar
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link active"
                      id="profile-tab"
                      data-toggle="tab"
                      href="#profile"
                      role="tab"
                      aria-controls="profile"
                      aria-selected="true"
                    >
                      Keuntungan Kamu Belajar di Niomic
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      className="nav-link"
                      id="contact-tab"
                      data-toggle="tab"
                      href="#contact"
                      role="tab"
                      aria-controls="contact"
                      aria-selected="false"
                    >
                      Syllabus
                    </a>
                  </li>
                </ul>
                <div className="tab-content" id="myTabContent">
                  <Home />
                  <Keuntungan />
                  <Silabus />
                </div>
              </div>
            </div>
          </div>
        </section>
        {this.props.index === "true" ? "" : <Footers />}
      </div>
    );
  }
}

export default Index;
