import React, { Component } from "react";
import { Image } from "react-bootstrap";
import { connect } from "react-redux";
import { requestGoogleAlurPendaftaran } from "../../actions/google-sheet";

var dataContent = [];
var img = [];
var judul = [];
var isi = [];
class Section1 extends Component {
  componentDidMount() {
    this.props.requestGoogleAlurPendaftaran();
  }

  getContent = data => {
    dataContent = data;
    var path = "";

    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (path = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (path = "");
      if (path === "img") {
        for (var a = 0; a < i; a++) {
          img.push(dataContent[a]);
        }
      } else if (path === "judul") {
        for (var b = img.length + 1; b < i; b++) {
          judul.push(dataContent[b]);
        }
      } else if (path === "isi") {
        for (var c = img.length + 1 + (judul.length + 1); c < i; c++) {
          isi.push(dataContent[c]);
        }
      }
    }
  };

  render() {
    const { responseGoogleAlurPendaftaran } = this.props;
    responseGoogleAlurPendaftaran
      ? this.getContent(responseGoogleAlurPendaftaran)
      : this.getContent([]);

    return (
      <div>
        <section className="o-hidden">
          <div className="container">
            <div className="row align-items-center justify-content-around">
              <div className="col-md-9 col-lg-5" data-aos="fade-in">
                <Image
                  src={img[0]}
                  alt="Image"
                  className="img-fluid rounded shadow"
                />
                <Image
                  src={img[1]}
                  alt="Image"
                  className="position-absolute p-0 col-4 col-xl-5 border border-white border-thick rounded-circle top left shadow-lg mt-5 ml-n5 ml-lg-n3 ml-xl-n5 d-none d-md-block"
                  datajarallaxelement="-20 0"
                />
              </div>
              <div className="col-md-9 col-lg-6 col-xl-5 mt-4 mt-md-5 mt-lg-0">
                <ol className="list-unstyled p-0">
                  <li className="d-flex align-items-start">
                    <h3 style={{ marginBottom: 30 }}>{judul[0]}</h3>
                  </li>
                  {isi.map((dataIsi, keyIsi) => {
                    return (
                      <li className="d-flex align-items-start" key={keyIsi}>
                        <div className="rounded-circle p-3 p-sm-4 d-flex align-items-center justify-content-center bg-success">
                          <div className="position-absolute text-white h5 mb-0">
                            <p>{keyIsi + 1} </p>
                          </div>
                        </div>
                        <div className="ml-3 ml-md-4">
                          <h4>{dataIsi[0]}</h4>
                          <p>
                            {dataIsi[1]}
                            {dataIsi[4] ? (
                              <a
                                href={dataIsi[3]}
                                target="_blank"
                                rel="noopener noreferrer"
                              >
                                {dataIsi[2]}
                              </a>
                            ) : (
                              <a href={dataIsi[3]}>{dataIsi[2]}</a>
                            )}
                          </p>
                        </div>
                      </li>
                    );
                  })}
                </ol>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleAlurPendaftaran: state.responseGoogleAlurPendaftaran
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleAlurPendaftaran: () => dispatch(requestGoogleAlurPendaftaran())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Section1);
