import React, { Component } from "react";
import { Image } from "react-bootstrap";

import { connect } from "react-redux";
let dataContent = [];
var tempDataContent = [];
class Content extends Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      input: ""
    };
  }
  handlingSearch = e => {
    tempDataContent = [];
    var data = dataContent.filter(
      item =>
        item.title.toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1
    );

    tempDataContent = data;
    this.setState({ loading: true, input: e.target.value });
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({ loading: false, input: "" });
  }

  handlingPush = (e, key) => {
    localStorage.setItem("SubContent", key);
  };

  render() {
    const { responseContentSelected, dataMateri } = this.props;
    dataContent = responseContentSelected
      ? responseContentSelected.setContent.id_list_materi
      : dataMateri[0]
        ? dataMateri[0].id_list_materi
        : [];
    if (this.state.loading === false) {
      tempDataContent = dataContent;
    }
    return (
      <div className="container">
        <div className="example">
          <div
            style={{
              zIndex: 999,
              position: "absolute",
              width: "100%",
              backgroundColor: "white"
            }}
          >
            <div style={{ width: 800 }}>
              <span
                style={{
                  fontSize: "1.7rem",
                  fontWeight: "bold",
                  color: "black",
                  lineHeight: "2.4rem"
                }}
              >
                {responseContentSelected
                  ? responseContentSelected.setContent.title
                  : dataMateri[0]
                    ? dataMateri[0].title
                    : ""}
              </span>
              <br />
              <span>
                {responseContentSelected
                  ? responseContentSelected.setContent.description
                  : dataMateri[0]
                    ? dataMateri[0].description
                    : ""}
              </span>
            </div>
            <div
              style={{
                borderRadius: 10,
                top: 0,
                marginBottom: 10,
                paddingRight: 55
              }}
            >
              <input
                type="text"
                onChange={e => {
                  this.handlingSearch(e);
                }}
                value={this.state.input}
                placeholder="Search Vidio..."
              />
            </div>
          </div>
          <br />
          <hr />
          <div className="row" style={{ marginTop: 70, marginRight: 3 }}>
            {tempDataContent.map((data, key) => {
              return (
                <a
                  href="courses"
                  onClick={e => {
                    this.handlingPush(e, data._id);
                  }}
                  key={key}
                  className="col-md-3 col-lg-3 mb-2 mb-md-3"
                >
                  <div className="card  hover-box-shadow">
                    <div className="d-block bg-gradient rounded-top position-relative">
                      <Image
                        className="card-img-top hover-fade-out"
                        src={data.image_youtube}
                        style={{ width: "100%", maxWidth: 600, height: "auto" }}
                        alt="img accompanying Treva testimonial"
                      />

                      <div
                        style={{
                          position: "absolute",
                          top: -2,
                          right: -2,
                          border: 0,
                          padding: 5,
                          paddingRight: 10,
                          paddingLeft: 10,
                          backgroundColor: "white",
                          borderRadius: 5
                        }}
                      >
                        <span>{data.id_list / 5}</span>
                      </div>
                    </div>
                    <div style={{ padding: 10 }}>
                      <span style={{ fontSize: "1rem" }}>{data.title}</span>
                    </div>
                  </div>
                </a>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseContentSelected: state.responseContentSelected
  };
};

export default connect(mapStateToProps)(Content);
