import React, { Component } from "react";
//const dataMateri = [1, 2, 3, 4, 5, 6, 7, 8, 5];
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { requestContentSelected } from "../../actions/content";
var dataMateri = [];
class SubContent extends Component {
  constructor() {
    super();
    this.state = {
      tempDataMateri: [],
      loading: false
    };
  }
  componentDidUpdate() {
    if (this.state.loading === false) {
      this.setState({ tempDataMateri: dataMateri, loading: true });
    }
  }
  handlingSearch = e => {
    var data = dataMateri.filter(
      item =>
        item.title.toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1
    );
    this.setState({ tempDataMateri: data });
  };

  handlingState = (key, data) => {
    this.props.requestContentSelected(key, data);
    localStorage.setItem("content", key);
  };
  render() {
    dataMateri = this.props
      ? this.props.data
      : this.props.dataMateri.id_list_materi;

    return (
      <div className="container" style={{ marginBottom: 20 }}>
        <div style={{ position: "relative", borderRadius: 10 }}>
          <input
            onChange={e => {
              this.handlingSearch(e);
            }}
            type="text"
            placeholder="Search Materi..."
          />
        </div>

        <div
          id="content"
          style={{
            height: 900,
            maxHeight: 900,
            overflow: "auto",
            overflowX: "hidden"
          }}
        >
          {this.state.tempDataMateri.map((data, key) => {
            return (
              <Link
                key={key}
                to="#"
                onClick={e => {
                  this.handlingState(data._id, data, e);
                }}
                className=" text-left card hover-box-shadow "
                style={{ padding: 10, height: "auto" }}
              >
                <div className="row ">
                  <div className="col-md-2">
                    <img src={data.image} alt={data.title} height={37} />{" "}
                  </div>
                  <div className="col-md-10">
                    <div
                      style={{
                        right: 13,
                        backgroundColor: "#fff",
                        color: "black"
                      }}
                    >
                      <span style={{ fontSize: 11 }}>
                        {data.id_list_materi.length} Module{" "}
                      </span>
                    </div>
                    <span style={{ fontSize: 14 }}>{data.title} </span>{" "}
                  </div>
                </div>
              </Link>
            );
          })}
        </div>
        <hr />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseContentSelected: state.responseContentSelected
  };
};
const mapDispatchToProps = dispatch => ({
  requestContentSelected: (key, data) =>
    dispatch(requestContentSelected(key, data))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SubContent);
