import React, { Component } from "react";
import { Image } from "react-bootstrap";
import { withRouter } from "react-router-dom";
let dataMateri = [];
class SubMenu extends Component {
  constructor() {
    super();
    this.state = {
      parsing: false
    }
  }
  handlingPush = (key) => {

    localStorage.setItem("SubContent", key);
    this.setState({ parsing: true });
    this.props.history.push("/courses#top");


  };
  render() {
    const { data } = this.props;
    dataMateri = data ? data : [];

    return (
      <section className="container">
        <span
          style={{
            color: "black",
            fontSize: 20,
            marginLeft: 20,
          }}
        >
          List Materi
        </span>
        <hr style={{ marginLeft: 10 }} />
        {dataMateri.map((data, key) => {
          return (
            <a
              style={{ marginBottom: 8 }}
              href={"#" + data.title}
              onClick={() => {
                this.handlingPush(data._id);
              }}
              key={key}
              className="niomic-card"
            >
              <div className="bg-relative">
                <span
                  className="niomic-badge"
                >{key + 1}</span>
                <Image
                  className="rounded img-fluid hover-fade-out"
                  src={data.image_youtube}
                  alt="blog.2.image"
                />


                <span className="">{data.title}
                  <div className="text-small text-muted mt-2 ">NIOMIC</div>
                </span>

              </div>
            </a>
          );
        })}
      </section>
    );
  }
}

export default withRouter(SubMenu);
