import React, { Component } from "react";
import { connect } from "react-redux";

import { requestHomeGoogle } from "../../actions/google-sheet";
import Headers from "../Headers";
import Background from "./Background";
import SectionTeknologi from "./SectionTeknologi";
import SectionSistemBelajar from "../sistem-belajar/Index";
import SectionQuote from "./SectionQuote";
import Section1 from "./Section1";
import SectionBatchSebelumnya from "./SectionBatchSebelumnya";
import Section2 from "./Section2";
import Section3 from "./Section3";
import Footers from "../Footers";
import BackToTop from "./BackToTop";
import WhatsappIcon from "./WhatsappIcon";

class Index extends Component {
  componentDidMount() {
    this.props.requestHomeGoogle();
  }

  render() {
    const { responseHomeGoogle } = this.props;

    if (responseHomeGoogle.code === 2000) {
      return (
        <div
          style={{ marginTop: "25%", height: 100, width: 500 }}
          className="card text-center align-items-center justify-content-around"
        >
          <p>{responseHomeGoogle.message}</p>
        </div>
      );
    }
    return (
      <div>
        <Headers index="true" />
        <Background responseGoogle={responseHomeGoogle} />
        <SectionTeknologi />
        <SectionSistemBelajar index="true" />
        <Section1 />
        <Section3 />
        <SectionQuote />
        <SectionBatchSebelumnya index="true" />
        <Section2 />
        <Footers />
        <WhatsappIcon link={"http://bit.ly/2ADYSvl"} />
        <BackToTop />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseHomeGoogle: state.responseHomeGoogle
  };
};

const mapDispatchToProps = dispatch => ({
  requestHomeGoogle: () => dispatch(requestHomeGoogle())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Index);
