import React, { Component } from "react";
import { connect } from "react-redux";
import { requestGoogleQuote } from "../../actions/google-sheet";
var dataContent = [];
class SectionQuote extends Component {
  componentDidMount() {
    this.props.requestGoogleQuote();
  }

  getContent = () => {
    this.props.responseGoogleQuote
      ? (dataContent = this.props.responseGoogleQuote)
      : (dataContent = []);
  };
  render() {
    this.getContent();

    return (
      <section className="bg-light">
        <div className="container">
          <div className="row col-md-12">
            {dataContent.map((data, key) => {
              return (
                <div className="col-md-6 text-center mb-4 mb-md-0" key={key}>
                  <blockquote className="blockquote p-0 border-0 text-body pr-xl-4">
                    &ldquo;
                    {data[0]}
                    &ldquo;
                  </blockquote>
                  <div className="d-flex flex-column align-items-center">
                    <h6 className="mb-1">{data[1]}</h6>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleQuote: state.responseGoogleQuote
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleQuote: () => dispatch(requestGoogleQuote())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SectionQuote);
