import React, { Component } from "react";
import { Image } from "react-bootstrap";
class Background extends Component {
  render() {
    var responseGoogle = this.props.responseGoogle;
    return (
      <div
        data-overlay
        className="bg-primary-3 jarallax text-white"
        data-jarallax
        data-speed="0.2"
      >
        <img
          src={responseGoogle[5]}
          alt="Background"
          className="jarallax-img opacity-30"
        />
        <section className="pb-0">
          <div className="container pb-5">
            <div className="row justify-content-center text-center">
              <div className="col-xl-8 col-lg-10 col-md-11">
                <a
                  data-fancybox
                  href={responseGoogle[6]}
                  className="btn btn-lg btn-light rounded-circle mb-4 mb-md-5"
                  data-aos="zoom-in"
                  data-aos-delay="400"
                >
                  <Image
                    src={responseGoogle[7]}
                    alt="Media Play Icon"
                    className="icon icon-lg  pl-1"
                    datainjectsvg="true"
                  />
                </a>
                <h1
                  className="display-3"
                  data-aos="fade-up"
                  data-aos-delay="100"
                >
                  {responseGoogle[0]}
                </h1>
                <p className="lead" data-aos="fade-up" data-aos-delay="200">
                  {responseGoogle[1]}
                </p>
                <p className="lead" data-aos="fade-up" data-aos-delay="200">
                  {responseGoogle[2]}
                </p>
                <div
                  className="d-flex flex-column flex-sm-row justify-content-center mt-4 mt-md-5"
                  data-aos="fade-up"
                  data-aos-delay="300"
                >
                  <a
                    href={responseGoogle[3] ? responseGoogle[3][1]: ""}
                    className="btn btn-primary btn-lg mx-sm-2 my-1 my-sm-0"
                  >
                    {responseGoogle[3] ? responseGoogle[3][0]: ""}
                  </a>
                  <a
                    href={responseGoogle[3] ? responseGoogle[4][1]: ""}
                    className="btn btn-outline-light btn-lg mx-sm-2 my-1 my-sm-0"
                  >
                    {responseGoogle[3] ? responseGoogle[4][0]: ""}
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="divider divider-bottom bg-white"></div>
        </section>
      </div>
    );
  }
}

export default Background;
