import React from "react";
import { ResponsiveEmbed } from "react-bootstrap";
function Article(props) {
  const { data } = props;
  return (
    <div>
      <ResponsiveEmbed
        style={{ height: data ? data.total_page_article : 0 }}
      >
        <embed
          title="Content Article Is Loading..."
          type="image/svg+xml"
          src={data ? data.link_article : ""}
        />
      </ResponsiveEmbed>
    </div>
  );
}

export default Article;
