import React, { Component } from "react";
import Footers from "../Footers";
import Headers from "../Headers";
import { connect } from "react-redux";
import { requestGoogleBatchSebelumnya } from "../../actions/google-sheet";
var dataContent = [];
var judul = [];
var isi = [];
var url = [];
var total = [];
class SectionBatchSebelumnya extends Component {
  componentDidMount() {
    this.props.requestGoogleBatchSebelumnya();
  }

  getContent = value => {
    this.props.responseGoogleBatchSebelumnya
      ? (dataContent = this.props.responseGoogleBatchSebelumnya)
      : (dataContent = []);

    var path = "";
    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (path = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (path = "");
      if (path === "judul") {
        for (var a = 0; a < i; a++) {
          judul.push(dataContent[a]);
        }
      } else if (path === "isi") {
        for (var b = judul.length + 1; b < i; b++) {
          total.push(dataContent[b]);
          if (value === "true") {
            if (b <= 6) {
              isi.push(dataContent[b]);
            }
          } else {
            isi.push(dataContent[b]);
          }
        }
      } else if (path === "url") {
        for (var c = judul.length + 1 + (total.length + 1); c < i; c++) {
          url.push(dataContent[c]);
        }
      }
    }
  };

  render() {
    this.getContent(this.props.index);
    return (
      <div>
        {this.props.index === "true" ? "" : <Headers />}
        <section>
          <div className="container">
            <div className="row section-title justify-content-center text-center">
              <div className="col-md-9 col-lg-8 col-xl-7">
                <h2>{judul[0]}</h2>
              </div>
            </div>
          </div>
          <div className="container justify-content-center">
            <div className="mt-5">
              <table className="table table-responsive-sm">
                <thead>
                  <tr>
                    <th scope="col">Gelombang</th>
                    <th scope="col">Fase Immersive</th>
                    <th scope="col">Akun</th>
                    <th scope="col">Kelas</th>
                    <th scope="col">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {isi.map((data, key) => {
                    return (
                      <tr key={key}>
                        <td>
                          <span style={{ color: "#f18f01" }}> <b>{data[0]}</b></span>
                        </td>
                        <td>{data[1]}</td>
                        <td>{data[2]}</td>
                        <td>{data[3]}</td>
                        <td>
                          {key === 0 ? (
                            <a
                              href="/registration"
                              rel="opener"
                              style={{ color: "#f18f01" }}
                            >
                              {data[4]}
                            </a>
                          ) : (
                            <span style={{ color: "#f18f01" }}>{data[4]}</span>
                          )}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
          {this.props.index === "true" ? (
            <div className="row justify-content-center mt-4 mt-md-5">
              <div className="col-auto">
                <div className="text-black links-black">
                  Lihat lengkap {total.length} batch sebelumnya?
                  <a href={url[0]}> Klik Disini</a>
                </div>
              </div>
            </div>
          ) : (
            <div> </div>
          )}
        </section>
        {this.props.index === "true" ? "" : <Footers />}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleBatchSebelumnya: state.responseGoogleBatchSebelumnya
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleBatchSebelumnya: () => dispatch(requestGoogleBatchSebelumnya())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SectionBatchSebelumnya);
