import React, { Component } from "react";
import { connect } from "react-redux";
import { requestGoogleKeuntungan } from "../../actions/google-sheet";
var dataKeuntungan = [];

class Keuntungan extends Component {
  handlingState = value => {
    if (value) {
      dataKeuntungan = value;
    }
  };

  componentDidMount() {
    this.props.requestGoogleKeuntungan();
  }
  render() {
    const { responseGoogleKeuntungan } = this.props;
    this.handlingState(responseGoogleKeuntungan);
    return (
      <div
        className="tab-pane fade show active"
        id="profile"
        role="tabpanel"
        aria-labelledby="profile-tab"
      >
        <div className="row align-items-center justify-content-around">
          {dataKeuntungan.map((item, i) => {
            return (
              <div key={i} className="col-lg-6 col-xl-6" data-aos="fade-left">
                <div
                  className="m-2 card card-body hover-box-shadow"
                  data-aos="fade-up"
                  dataaosdelay="100"
                >
                  <div className="media rounded align-items-center pl-3 pr-1 pl-md-1 pr-md-1 py-2 bg-white">
                    <img
                      src={item[0]}
                      alt="Monitor icon"
                      className="icon mr-3 "
                    />
                    <h5 className="mb-0">{item[1]}</h5>
                  </div>
                  <p
                    style={{
                      backgroundColor: "#fff",
                      paddingLeft: 10,
                      paddingRight: 10
                    }}
                  >
                    {item[2]}
                  </p>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleKeuntungan: state.responseGoogleKeuntungan
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleKeuntungan: () => dispatch(requestGoogleKeuntungan())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Keuntungan);
