import React, { Component } from "react";
import { connect } from "react-redux";
import { requestLogin } from "../../actions/login";
import errorHandling from "../../exceptions/errorapi/Index.js";
import swal from "sweetalert";

import { withRouter } from "react-router-dom";
//import { requestContent } from "../../actions/content";
import { Image } from "react-bootstrap";
var Recaptcha = require("react-recaptcha");
var recaptchaToken = "";
let recaptchaInstance;

const verifyCallback = response => {
  return (recaptchaToken = response);
};

const resetRecaptcha = () => {
  recaptchaInstance.reset();
};

const callback = () => {
  return true;
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      loadingStatus: false
    };
    this.handlingLoginForm = this.handlingLoginForm.bind(this);
  }

  UNSAFE_componentWillMount() {
    const token = sessionStorage.getItem("browser_default") || "";
    const name = sessionStorage.getItem("browser_name") || "";
    if (token && name) {
      this.handlingState("loadingStatus", true);
      console.log(token);
      return this.props.history.push("/materi");
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { responseLogin } = nextProps;
    if (responseLogin.hasOwnProperty("code") && responseLogin.code === 1000) {
      // localStorage.removeItem("promo");
      // localStorage.removeItem("promoDate");
      // localStorage.removeItem("normalDate");
      return this.props.history.push("/materi");
    } else {
      this.handlingState("loadingStatus", false);
      swal("Proses Login Gagal", `${responseLogin.message}`, "error");
      resetRecaptcha();
      this.handlingState("email", "");
      this.handlingState("password", "");
    }
  }

  componentDidMount() {
    resetRecaptcha();
  }

  handlingLoginForm(e) {
    e.preventDefault();
    if (
      this.state.email.match(/@/g) &&
      this.state.password.length > 7 &&
      recaptchaToken
    ) {
      this.props.requestLogin(
        this.state.email,
        this.state.password,
        recaptchaToken
      );
      this.handlingState("loadingStatus", true);
    } else {
      swal(
        "Proses Login Gagal",
        `${errorHandling.exceptions.errorLogin.message}`,
        "error"
      );
      this.handlingState("email", "");
      this.handlingState("password", "");
    }
  }

  handlingState(attribute, value) {
    this.setState((state, props) => {
      return { [attribute]: value };
    });
  }

  handler = () => {
    //  console.log(recaptchaToken);
  };

  render() {
    //  console.log(this.props.responseLogin);
    return (
      <div
        data-overlay
        className="min-vh-100 bg-light d-flex flex-column justify-content-md-center"
      >
        <section className="py-3">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-xl-4 col-lg-5 col-md-6">
                <div className="card card-body shadow">
                  <h1 className="h5 text-center">Sign In</h1>
                  <div>
                    <div className="form-group">
                      <input
                        type="email"
                        className="form-control"
                        name="email"
                        placeholder="Email Address"
                        value={this.state.email}
                        onChange={e =>
                          this.handlingState("email", e.target.value)
                        }
                      />
                    </div>
                    <div className="form-group">
                      <input
                        type="password"
                        className="form-control"
                        name="sign-up-password"
                        placeholder="Password"
                        autoComplete="current-password"
                        value={this.state.password}
                        onChange={e =>
                          this.handlingState("password", e.target.value)
                        }
                      />
                      {/*  <div className="text-right text-small mt-2">
                        <a href="account-forgot-password.html">
                          Forgot Password?
                        </a>
                      </div>
                      */}
                    </div>
                    <div className="form-group">
                      <Recaptcha
                        sitekey={`${process.env.REACT_APP_WEB_SITEKEY}`}
                        ref={e => (recaptchaInstance = e)}
                        render="explicit"
                        hl="id"
                        onloadCallback={callback}
                        verifyCallback={verifyCallback}
                      />
                    </div>

                    {!this.state.loadingStatus ? (
                      <button
                        className="btn btn-primary btn-block"
                        onClick={e => this.handlingLoginForm(e)}
                      >
                        Login
                      </button>
                    ) : (
                      <div>
                        <Image
                          src="https://s3-ap-southeast-1.amazonaws.com/niomic/img/loading.gif"
                          avatar="true"
                        />
                        <br />
                        <span>Mohon ditunggu . . . </span>
                      </div>
                    )}
                  </div>
                </div>
                <div className="text-center text-small mt-3">
                  Don't have an account? <a href="registration">Sign up here</a>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { responseLogin: state.responseLogin };
};

const mapDispatchToProps = dispatch => ({
  requestLogin: (email, password, recaptchaToken) =>
    dispatch(requestLogin(email, password, recaptchaToken))
  //  requestContent: token => dispatch(requestContent(token))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Login));
