import React, { Component } from "react";
import { Collapse } from "react-bootstrap";
import { connect } from "react-redux";
import { requestGoogleSillabus } from "../../actions/google-sheet";
var dataContent = [];
var dataAll = [];
var tampilData = [];
var tampilIsi = [];
class Silabus extends Component {
  constructor(props) {
    super(props);
    this.state = { dataAlls: [] };
  }

  componentDidMount() {
    this.props.requestGoogleSillabus();
  }

  getContent = () => {
    this.props.responseGoogleSillabus
      ? (dataContent = this.props.responseGoogleSillabus)
      : (dataContent = []);
    for (var i = 1; i < dataContent.length; i++) {
      dataAll.push({
        name: dataContent[i][0],
        value: false,
        isi: dataContent[i]
      });
    }
  };

  onCollapse = valueName => {
    var convertDataAll = [];

    this.state.dataAlls.length === 0
      ? (convertDataAll = dataAll)
      : (convertDataAll = this.state.dataAlls);

    for (var a = 0; a < convertDataAll.length; a++) {
      if (convertDataAll[a].name === valueName) {
        convertDataAll[a].value === true
          ? (convertDataAll[a] = {
              name: convertDataAll[a].name,
              value: false,
              isi: convertDataAll[a].isi
            })
          : (convertDataAll[a] = {
              name: convertDataAll[a].name,
              value: true,
              isi: convertDataAll[a].isi
            });
      } else {
        convertDataAll[a] = {
          name: convertDataAll[a].name,
          value: false,
          isi: convertDataAll[a].isi
        };
      }
    }

    this.setState({
      dataAlls: convertDataAll
    });

    dataAll = [];
  };

  render() {
    this.getContent();

    this.state.dataAlls.length === 0
      ? (tampilData = dataAll)
      : (tampilData = this.state.dataAlls);

    return (
      <div
        className="tab-pane fade"
        id="contact"
        role="tabpanel"
        aria-labelledby="contact-tab"
      >
        <section className="bg-light o-hidden" style={{ paddingTop: 0 }}>
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-md-10 col-lg-8">
                <div id="faq-accordion">
                  {tampilData.map((data, key) => {
                    tampilIsi = data.isi;
                    tampilIsi = tampilIsi.filter(item => item !== data.name);

                    return (
                      <div className="card mb-2 mb-md-3" key={key}>
                        <a
                          onClick={() => this.onCollapse(data.name)}
                          aria-controls="accordion-1"
                          aria-expanded={data.value}
                          className="p-3 p-md-4"
                          role="button"
                          data-toggle="collapse"
                          href="/"
                        >
                          <div className="d-flex justify-content-between align-items-center">
                            <h6 className="mb-0 mr-2">{data.name}</h6>
                            <img
                              src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-caret-right.svg"
                              alt="Caret Right"
                              className="icon icon-sm"
                              datainjectsvg="true"
                            />
                          </div>
                        </a>

                        <Collapse in={data.value}>
                          <div
                            className="collapse"
                            id="accordion-1"
                            data-parent="#faq-accordion"
                          >
                            <div className="px-3 px-md-4 pb-3 pb-md-4">
                              {tampilIsi.map((data2, key) => {
                                return (
                                  <div key={key} className="row col-md-12 ">
                                    <div className="col-auto">
                                      <span>{key + 1} </span>
                                    </div>
                                    <div className="px-md-1 col-md-10">
                                      <span>{data2} </span>
                                    </div>
                                  </div>
                                );
                              })}
                            </div>
                          </div>
                        </Collapse>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleSillabus: state.responseGoogleSillabus
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleSillabus: () => dispatch(requestGoogleSillabus())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Silabus);
