import React, { Component } from "react";
import Background from "../landing-page/Background";
import { connect } from "react-redux";

import { requestHome } from "../../actions/home";

import YouTube from "react-youtube";

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  _onReady(event) {
    event.target.pauseVideo();
  }

  render() {
    const opts = {
      height: "390",
      width: "640",
      playerVars: {
        autoplay: 0
      }
    };
    return (
      <div>
        <Background responseHome={this.props.responseHome.message} />
        <br />
        <br /> <br />
        <p> Youtube Vidios </p>
        <YouTube videoId="qZUAGX3_Coc" opts={opts} onReady={this._onReady} />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseHome: state.responseHome
  };
};

const mapDispatchToProps = dispatch => ({
  requestHome: () => dispatch(requestHome())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Index);
