import React, { Component } from "react";
import Headers from "../HeaderCourses";
import Footers from "../Footers";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { requestContent, requestContentSelected } from "../../actions/content";
import Content from "./Content";
class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  UNSAFE_componentWillMount() {
    const token = sessionStorage.getItem("browser_default") || "";
    const name = sessionStorage.getItem("browser_name") || "";
    if (token && name) {
      this.props.requestContent(token);
    } else if (
      !token ||
      !name ||
      (this.props.responseContent.hasOwnProperty("code") &&
        this.props.responseContent.code !== 1000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }
  componentWillReceiveProps(nextProps) {
    const { responseContent } = nextProps;
    // if (responseContentSelected) {
    //   return this.props.history.push("/materi");
    // }
    console.log(sessionStorage.getItem("content"));
    if (
      responseContent.hasOwnProperty("code") &&
      (responseContent.code === 2000 || responseContent.code === 9000)
    ) {
      this.handlingLogout();
      return this.props.history.push("/login");
    }
  }

  handlingLogout() {
    sessionStorage.removeItem("browser_default");
    sessionStorage.removeItem("browser_name");
  }

  render() {
    const dataItem = sessionStorage.getItem("content");
    const dataListMateri = this.props.responseContent.message
      ? this.props.responseContent.message[dataItem].id_list_materi
      : [];

    return (
      <div className="bg-light container">
        <Headers listMateri={false} />
        <Content data={dataListMateri} />

        <Footers />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseContent: state.responseContent,
    responseContentSelected: state.responseContentSelected
  };
};

const mapDispatchToProps = dispatch => ({
  requestContent: token => dispatch(requestContent(token)),
  requestContentSelected: (key, data) => {
    dispatch(requestContentSelected(key, data));
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Index)
);
