export const responseContent = (state = "", action) => {
  switch (action.type) {
    case "RESPONSE_CONTENT":
      return action.payload;
    default:
      return state;
  }
};

export const responseContentSelected = (state = "", action) => {
  switch (action.type) {
    case "RESPONSE_CONTENT_SELECTED":
      return action.payload;
    default:
      return state;
  }
};
