import request from "superagent";
import { responseLogin } from "./index";
import errorHandling from "../exceptions/errorapi/Index.js";

export const requestLogin = (email, password, recaptchaToken) => {
  return dispatch => {
    try {
      request
        .post(`${process.env.REACT_APP_IP}/user/login`)
        .set("Content-Type", "application/json")
        .send({
          email: email,
          password: password,
          recaptchaToken: recaptchaToken
        })
        .end((err, res) => {
          if (res) {
            if (res.body.code === 1000) {
              sessionStorage.setItem("browser_default", res.body.token);
              sessionStorage.setItem("browser_name", res.body.name);
              dispatch(responseLogin(res.body));
            } else if (res.body.code === 9000 || res.body.code === 2000) {
              dispatch(responseLogin(res.body));
            } else {
              dispatch(responseLogin(errorHandling.exceptions.errorLogin));
            }
          } else {
            dispatch(responseLogin(errorHandling.exceptions.errorLogin));
          }
        });
    } catch (err) {
      dispatch(responseLogin(errorHandling.exceptions.errorLogin));
    }
  };
};
