import React, { Component } from "react";

import { connect } from "react-redux";
import { requestGoogleFooter } from "../actions/google-sheet";
var dataContent = [];

var listPages = [];
var listPagesTitle = [];
var dataKunjungi = [];
var dataKunjungiTitle = [];
var dataContact = [];
var dataOfficialOffice = [];
var totalDataKunjungi = [];

var copyright = [];

class Footers extends Component {
  componentDidMount() {
    this.props.requestGoogleFooter();
  }

  getContent = () => {
    this.props.responseGoogleFooter
      ? (dataContent = this.props.responseGoogleFooter)
      : (dataContent = []);
    var path = "";

    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (path = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (path = "");
      if (path === "kunjungi") {
        for (var a = 0; a < i; a++) {
          totalDataKunjungi.push(dataContent[a]);
          a === 0
            ? dataKunjungiTitle.push(dataContent[a])
            : dataKunjungi.push(dataContent[a]);
        }
      } else if (path === "listPages") {
        for (var b = totalDataKunjungi.length + 1; b < i; b++) {
          b === totalDataKunjungi.length + 1
            ? listPagesTitle.push(dataContent[b])
            : listPages.push(dataContent[b]);
        }
      } else if (path === "hubungiKami") {
        for (
          var c =
            totalDataKunjungi.length +
            1 +
            (listPages.length + 1) +
            listPagesTitle.length;
          c < i;
          c++
        ) {
          dataContact.push(dataContent[c]);
        }
      } else if (path === "officialOffice") {
        for (
          var d =
            dataContact.length +
            1 +
            totalDataKunjungi.length +
            1 +
            (listPages.length + 1) +
            listPagesTitle.length;
          d < i;
          d++
        ) {
          dataOfficialOffice.push(dataContent[d]);
        }
      } else if (path === "copyright") {
        for (
          var e =
            dataContact.length +
            1 +
            totalDataKunjungi.length +
            1 +
            (listPages.length + 1) +
            listPagesTitle.length +
            (dataOfficialOffice.length + 1);
          e < i;
          e++
        ) {
          copyright.push(dataContent[e]);
        }
      }
    }
  };

  render() {
    dataKunjungi = [];
    this.getContent();
    return (
      <footer
        className="bg-primary-3 text-white links-white pb-4 footer-1"
        style={{ paddingTop: 60 }}
      >
        <div className="container">
          <div className="row col-md-12">
            <div className="col-md-2 mb-4 ">
              <p>{dataKunjungiTitle[0]}</p>
              <ul className="nav flex-row flex-md-column">
                <li className="nav-item mr-3 mr-md-0">
                  {dataKunjungi.map((data, key) => {
                    return (
                      <a
                        key={key}
                        style={{ fontSize: 14 }}
                        id="link-lower-employers"
                        href={data[1]}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="nav-link fade-page px-0 py-2"
                      >
                        <img
                          src={data[2]}
                          height="18px"
                          style={{ marginRight: 5 }}
                          alt=""
                        />
                        {data[0]}
                      </a>
                    );
                  })}
                </li>
              </ul>
            </div>

            <div className="col-md-2 mb-4 ">
              <p>{listPagesTitle}</p>
              <ul className="nav flex-row flex-md-column">
                <li className="nav-item mr-3 mr-md-0">
                  {listPages.map((data, key) => {
                    return (
                      <span key={key} className="nav-link fade-page px-0 py-0">
                        <a
                          style={{ fontSize: 14 }}
                          id="link-lower-employers"
                          href={data[1]}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          {data[0]}
                        </a>
                        <br />
                      </span>
                    );
                  })}
                </li>
              </ul>
            </div>

            <div className="col-md-3 mb-4 ">
              <b>{dataContact[0]}</b>
              <br /> <br />
              <ul className="nav flex-row flex-md-column">
                <li className="nav-item mr-3 mr-md-0">
                  {dataContact.map((tampilContact, keyContact) => {
                    return (
                      <span key={keyContact}>
                        {keyContact === 0 ? (
                          ""
                        ) : (
                          <span
                            style={{ fontSize: 13 }}
                            id="link-lower-tos"
                            rel="opener"
                            className="nav-link fade-page px-0 py-0"
                          >
                            {keyContact % 3 === 0 ? (
                              <p
                                style={{ fontSize: 13 }}
                                id="link-lower-tos"
                                rel="opener"
                                className="nav-link fade-page px-0 py-0"
                              >
                                {" "}
                                {tampilContact}{" "}
                              </p>
                            ) : (
                              tampilContact
                            )}
                          </span>
                        )}
                      </span>
                    );
                  })}
                </li>
              </ul>
            </div>

            <div className="col-md-5 mb-4 ">
              <b>{dataOfficialOffice[0]}</b>
              <br /> <br />
              <ul className="nav flex-row flex-md-column">
                <li className="nav-item mr-3 mr-md-0">
                  {dataOfficialOffice.map((tampiloffice, keyoffice) => {
                    return (
                      <span key={keyoffice}>
                        {keyoffice === 0 ? (
                          ""
                        ) : (
                          <p
                            style={{ fontSize: 14, marginTop: 0 }}
                            id="link-lower-google"
                            rel="opener"
                            className="nav-link fade-page px-0 py-0"
                          >
                            {tampiloffice}
                          </p>
                        )}
                      </span>
                    );
                  })}
                </li>
              </ul>
            </div>
          </div>
          <hr style={{ marginTop: 0 }} />
          <div className="row flex-column flex-lg-row align-items-center justify-content-center justify-content-lg-between text-center text-lg-left">
            <div className="col-auto">
              <div className="d-flex flex-column flex-sm-row align-items-center text-small">
                <div className="text-muted">&copy; {copyright[0]}</div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleFooter: state.responseGoogleFooter
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleFooter: () => dispatch(requestGoogleFooter())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Footers);
