import React from "react";
import { Image } from "react-bootstrap";
const listMateri = [1, 2, 3, 4, 5, 6, 7, 8, 9, 1, 1, 2, 3, 4];
function Content(props) {
  const dataListMateri = props.data ? props.data : [];
  return (
    <section className="container">
      <div className=" row justify-content-center  ">
        <div className="col-md-6 ">
          {dataListMateri.map((data, key) => {
            return (
              <a
                style={{ marginBottom: 8 }}
                href="/courses"
                key={key}
                className="card  hover-box-shadow  "
              >
                <div className=" row justify-content-left ">
                  <div className="row">
                    <div className="col-md-3">
                      <div className="d-flex bg-gradient rounded-top position-relative">
                        <Image
                          className="card-img-top hover-fade-out"
                          src={data.image_youtube}
                          alt="img accompanying Treva testimonial"
                        />

                        <span className="icon icon-sm m-lg-0 text-white">
                          {key + 1}{" "}
                        </span>
                      </div>
                    </div>
                    <div className="col-md-9 ">
                      <span className="h6 ">{data.title}</span>
                    </div>
                  </div>
                </div>
              </a>
            );
          })}
        </div>
        <div className="col-md-6 ">
          {listMateri.map((data, key) => {
            return (
              <a
                style={{ marginBottom: 8 }}
                href="/courses"
                key={key}
                className="card  hover-box-shadow  "
              >
                <div className=" row justify-content-left ">
                  <div className="row">
                    <div className="col-md-3">
                      <div className="d-flex bg-gradient rounded-top position-relative">
                        <Image
                          className="card-img-top hover-fade-out"
                          src="assets/img/case-studies/thumb-5.jpg"
                          alt="img accompanying Treva testimonial"
                        />

                        <span className="icon icon-sm m-lg-0 text-white">
                          {key + 15}{" "}
                        </span>
                      </div>
                    </div>
                    <div className="col-md-9 ">
                      <span className="h6 ">
                        Making the most of team-building sessions
                      </span>
                    </div>
                  </div>
                </div>
              </a>
            );
          })}
        </div>
      </div>
    </section>
  );
}

export default Content;
