import React, { Component } from "react";
import { connect } from "react-redux";
import { requestGoogleMetodePembayaran } from "../../actions/google-sheet";
var dataContent = [];
var pembayaranlangsung = [];
var pembayarantokped = [];
var note = [];
var konfirmasipembayaran = [];
class CaraPembayaranTokped extends Component {
  componentDidMount() {
    this.props.requestGoogleMetodePembayaran();
  }

  getContent = () => {
    this.props.responseGoogleMetodePembayaran
      ? (dataContent = this.props.responseGoogleMetodePembayaran)
      : (dataContent = []);
    var header = "";
    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (header = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (header = "");
      if (header === "pembayaranlangsung") {
        for (var a = 0; a < i; a++) {
          pembayaranlangsung.push(dataContent[a]);
        }
      } else if (header === "pembayarantokped") {
        for (var b = pembayaranlangsung.length + 1; b < i; b++) {
          pembayarantokped.push(dataContent[b]);
        }
      } else if (header === "konfirmasipembayaran") {
        for (
          var c = pembayaranlangsung.length + 1 + (pembayarantokped.length + 1);
          c < i;
          c++
        ) {
          konfirmasipembayaran.push(dataContent[c]);
        }
      } else if (header === "note") {
        for (
          var d =
            pembayaranlangsung.length +
            1 +
            (pembayarantokped.length + 1) +
            (konfirmasipembayaran.length + 1);
          d < i;
          d++
        ) {
          note.push(dataContent[d]);
        }
      }
    }
  };

  render() {
    this.getContent();

    return (
      <div>
        <section className="o-hidden">
          <div className="container">
            <div className="row justify-content-around">
              <div className="col-md-9 col-lg-6" data-aos="fade-in">
                <h3 style={{ marginBottom: 20 }}>{pembayaranlangsung[0]}</h3>
                <h6>
                  {pembayaranlangsung[1] ? pembayaranlangsung[1][0] : ""}
                  <br />
                  {pembayaranlangsung[1] ? pembayaranlangsung[1][1] : ""}
                </h6>

                {pembayaranlangsung[2]
                  ? pembayaranlangsung[2].map((dataImage, keyImage) => {
                      return (
                        <img
                          key={keyImage}
                          alt="Bank BRI"
                          src={dataImage}
                          width="150"
                          style={{ marginRight: 20 }}
                        />
                      );
                    })
                  : ""}
                <p style={{ marginTop: 20 }}>
                  {pembayaranlangsung[3]
                    ? pembayaranlangsung[3].map((dataNoRek, keyNoRek) => {
                        return (
                          <span key={keyNoRek}>
                            {dataNoRek} <br />{" "}
                          </span>
                        );
                      })
                    : ""}
                </p>

                <p>
                  {pembayaranlangsung[4]
                    ? pembayaranlangsung[4].map((dataContoh, keyContoh) => {
                        return (
                          <span key={keyContoh}>
                            {" "}
                            {keyContoh === 0 ? (
                              <b>
                                {dataContoh} <br />
                              </b>
                            ) : (
                              <span>
                                {" "}
                                {dataContoh} <br />{" "}
                              </span>
                            )}{" "}
                          </span>
                        );
                      })
                    : ""}
                </p>
                <p>
                  {pembayaranlangsung[5]
                    ? pembayaranlangsung[5].map(
                        (dataContohPembayaran, keyContohPembayaran) => {
                          return (
                            <span key={keyContohPembayaran}>
                              {" "}
                              {dataContohPembayaran} <br />{" "}
                            </span>
                          );
                        }
                      )
                    : ""}
                </p>
              </div>
              <div className="col-md-9 col-lg-6" data-aos="fade-in">
                <h3 style={{ marginBottom: 20 }}>{pembayarantokped[0]}</h3>
                {pembayarantokped[1]
                  ? pembayarantokped[1].map((dataimg, keyimg) => {
                      return (
                        <img
                          key={keyimg}
                          alt="Logo Tokped"
                          src={dataimg}
                          width={keyimg === 0 ? "50" : "150"}
                          style={{ marginRight: 20 }}
                        />
                      );
                    })
                  : ""}

                <p style={{ marginTop: 20 }}>{pembayarantokped[2]}</p>
                <p style={{ marginTop: 20 }}>
                  {pembayarantokped[3]
                    ? pembayarantokped[3].map((dataLisensi, keylisensi) => {
                        return (
                          <span key={keylisensi}>
                            {dataLisensi} <br />{" "}
                          </span>
                        );
                      })
                    : ""}
                </p>
                <p>
                  <b>{pembayarantokped[4]}</b>
                </p>
                <p>
                  {pembayarantokped[5]
                    ? pembayarantokped[5].map(
                        (
                          dataContohPembayaranTokped,
                          keyContohPembayaranTokped
                        ) => {
                          return (
                            <span key={keyContohPembayaranTokped}>
                              {" "}
                              {keyContohPembayaranTokped + 1}.{" "}
                              {dataContohPembayaranTokped} <br />{" "}
                            </span>
                          );
                        }
                      )
                    : ""}
                </p>
              </div>
            </div>
            <p style={{ marginTop: 40 }}>
              {konfirmasipembayaran[0] ? konfirmasipembayaran[0][0] : ""}
              <a
                href={konfirmasipembayaran[0] ? konfirmasipembayaran[0][2] : ""}
                target="_blank"
                rel="noopener noreferrer"
              >
                {konfirmasipembayaran[0] ? konfirmasipembayaran[0][1] : ""}
              </a>
            </p>
            <p>
              {note[0]
                ? note[0].map((datanote, keynote) => {
                    return (
                      <span key={keynote}>
                        {keynote === 0 ? datanote : <b>{datanote}</b>}
                      </span>
                    );
                  })
                : ""}
            </p>
          </div>
        </section>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleMetodePembayaran: state.responseGoogleMetodePembayaran
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleMetodePembayaran: () => dispatch(requestGoogleMetodePembayaran())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CaraPembayaranTokped);
