import React, { Component } from "react";

import { connect } from "react-redux";
import { requestGoogleTeknologi } from "../../actions/google-sheet";
var dataContent = [];
var head = [];
var image = [];
class SectionTeknologi extends Component {
  componentDidMount() {
    this.props.requestGoogleTeknologi();
  }

  getContent = () => {
    this.props.responseGoogleTeknologi
      ? (dataContent = this.props.responseGoogleTeknologi)
      : (dataContent = []);
    var path = "";

    for (var i = 0; i < dataContent.length; i++) {
      dataContent
        ? (path = JSON.stringify(dataContent[i]).replace(/[^\w\s]/gi, ""))
        : (path = "");
      if (path === "head") {
        for (var a = 0; a < i; a++) {
          head.push(dataContent[a]);
        }
      } else if (path === "image") {
        for (var b = head.length + 1; b < i; b++) {
          image.push(dataContent[b]);
        }
      }
    }
  };

  render() {
    this.getContent();

    return (
      <section className="pb-5 pt-5">
        <div className="container">
          <div className="row section-title justify-content-center text-center">
            <div className="col-md-9 col-lg-8 col-xl-7">
              <h3 className="display-4">{head[0]}</h3>
              <div className="lead">
                {head[1]}
                <br />
                {head[2]}
              </div>
            </div>
          </div>

          <div className="row justify-content-center">
            <div className="col-auto">
              <ul className="list-unstyled d-flex flex-wrap justify-content-center mb-0 align-items-center">
                {image.map((data, key) => {
                  return (
                    <li className="my-3 mx-3 mx-lg-4" key={key}>
                      <img src={data[0]} height="108px" alt={data[1]} />
                    </li>
                  );
                })}
              </ul>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleTeknologi: state.responseGoogleTeknologi
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleTeknologi: () => dispatch(requestGoogleTeknologi())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SectionTeknologi);
