import React, { Component } from "react";
import { Collapse } from "react-bootstrap";
import { withRouter } from "react-router-dom";
import Head from "./Head";
class HeaderCourses extends Component {
  constructor() {
    super();
    this.state = {
      open: false
    };
  }
  handlingLogout = () => {
    sessionStorage.removeItem("browser_default");
    sessionStorage.removeItem("browser_name");
  };
  collapse = () => {
    if (this.state.open === false) {
      this.setState({
        open: true
      });
    } else {
      this.setState({
        open: false
      });
    }
  };
  componentDidMount() {
    var initialPos = 0;
    var whiteHero = ""; // should be string of 'true' when hero is white image
    window.onload = function(e) {
      if (window.scrollY > 0) {
        initialPos = window.scrollY;
      }
    };

    var logoWhite = document.getElementById("niomic-logo-white");
    var logoBlack = document.getElementById("niomic-logo-black");
    var navbarMobile = document.getElementById("navbar-mobile");

    if (whiteHero === "true") {
      logoWhite.style.display = "none";
      logoBlack.style.display = "block";
    }

    if (whiteHero !== "true") {
      logoWhite.style.display = "block";
      logoBlack.style.display = "none";
    }

    navbarMobile.addEventListener("click", function() {
      var optionOpen = navbarMobile.getAttribute("aria-expanded");
      var logoWhite = document.getElementById("niomic-logo-white");
      var logoBlack = document.getElementById("niomic-logo-black");
      if (whiteHero === "true") {
        logoWhite.style.display = "none";
        logoBlack.style.display = "block";
      } else {
        if (optionOpen === "false") {
          logoWhite.style.display = "none";
          logoBlack.style.display = "block";
        } else {
          logoWhite.style.display = "block";
          logoBlack.style.display = "none";
        }
      }
    });

    window.addEventListener("scroll", function(ev) {
      var scrollpos = window.scrollY;

      var logoWhite = document.getElementById("niomic-logo-white");
      var logoBlack = document.getElementById("niomic-logo-black");

      if (scrollpos === 0 && initialPos <= 700 && whiteHero !== "true") {
        logoWhite.style.display = "block";
        logoBlack.style.display = "none";
      } else {
        logoWhite.style.display = "none";
        logoBlack.style.display = "block";
      }
    });
  }
  render() {
    return (
      <nav
        className={
          this.props.index === "true"
            ? "navbar navbar-expand-lg navbar-dark"
            : "navbar navbar-expand-lg navbar-light"
        }
        data-overlay
        data-sticky="top"
      >
        <Head />
        <div className="container">
          <a
            className="navbar-brand navbar-brand-dynamic-color fade-page"
            href="materi"
          >
            <img
              id="niomic-logo-white"
              alt="Niomic"
              style={{ maxHeight: 40, height: 40 }}
              src={
                this.props.index === "true"
                  ? "https://s3-ap-southeast-1.amazonaws.com/niomic/img-v1/c_niomic_w.png"
                  : "https://s3-ap-southeast-1.amazonaws.com/niomic/img-v1/c_niomic_b.png"
              }
            />
            <img
              id="niomic-logo-black"
              alt="Niomic"
              style={{ maxHeight: 40, height: 40 }}
              src="https://s3-ap-southeast-1.amazonaws.com/niomic/img-v1/c_niomic_b.png"
            />
          </a>
          <div className="d-flex align-items-center order-lg-3">
            <button
              id="navbar-mobile"
              aria-label="Toggle navigation"
              className="navbar-toggler"
              aria-controls="collapseExample"
              data-toggle="collapse"
              onClick={() => this.collapse()}
              aria-expanded={this.state.open}
              type="button"
            >
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                className="injected-svg navbar-toggler-open icon icon-sm"
                data-src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-menu.svg"
              >
                <path
                  d="M3 12H21"
                  stroke="#2C3038"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></path>
                <path
                  d="M3 6H21"
                  stroke="#2C3038"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></path>
                <path
                  d="M3 18H21"
                  stroke="#2C3038"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></path>
              </svg>
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
                className="injected-svg navbar-toggler-close icon icon-sm"
                data-src="https://asset-niomic.s3-ap-southeast-1.amazonaws.com/niomicv3/assets/img/icons/interface/icon-x.svg"
              >
                <path
                  d="M18 6L6 18"
                  stroke="#2C3038"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></path>
                <path
                  d="M6 6L18 18"
                  stroke="#2C3038"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></path>
              </svg>
            </button>
          </div>
          <Collapse in={this.state.open}>
            <div
              className="collapse navbar-collapse order-3 order-lg-2 justify-content-lg-end"
              id="collapseExample"
            >
              <ul className="navbar-nav my-3 my-lg-0">
                <li className="nav-item">
                  <a
                    className=" nav-link nav-item "
                    href="/materi"
                    role="button"
                  >
                    Materi
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    className=" nav-link nav-item "
                    onClick={() => this.handlingLogout()}
                    role="button"
                    href="/"
                  >
                    Logout
                  </a>
                </li>
              </ul>
            </div>
          </Collapse>
        </div>
      </nav>
    );
  }
}

export default withRouter(HeaderCourses);
