import request from "superagent";
import { responseHome } from "./index";
import errorHandling from "../exceptions/errorapi/Index";

export const requestHome = token => {
  return dispatch => {
    try {
      request
        .get(`${process.env.REACT_APP_IP}/web/homepage`)
        .set("Content-Type", "application/json")
        .end((err, res) => {
          if (res.body.code === 1000 || res.body.code === 2000) {
            dispatch(responseHome(res.body));
          } else {
            dispatch(responseHome(errorHandling.exceptions.errorGlobal));
          }
        });
    } catch (err) {
      dispatch(responseHome(errorHandling.exceptions.errorGlobal));
    }
  };
};
