import React, { Component } from "react";

import { connect } from "react-redux";
import { requestGoogleBeforeFooter } from "../../actions/google-sheet";
var dataContent = [];
class Section2 extends Component {
  componentDidMount() {
    this.props.requestGoogleBeforeFooter();
  }
  getContent = () => {
    this.props.responseGoogleBeforeFooter
      ? (dataContent = this.props.responseGoogleBeforeFooter)
      : (dataContent = []);
  };

  render() {
    this.getContent();
    return (
      <section className="pb-0">
        <div className="container">
          <div className="row section-title justify-content-center text-center">
            <div className="col-md-9 col-lg-8 col-xl-7">
              <h3 className="display-4">{dataContent[0]}</h3>
              <div className="lead">{dataContent[1]}</div>
            </div>
          </div>
          <div className="row justify-content-center text-center mt-md-n4">
            <div className="col-auto">
              <a href={dataContent[2]} className="btn btn-primary btn-lg">
                {dataContent[3]}
              </a>
            </div>
          </div>
        </div>
        <div className="divider divider-bottom bg-primary-3 mt-5"></div>
      </section>
    );
  }
}

const mapStateToProps = state => {
  return {
    responseGoogleBeforeFooter: state.responseGoogleBeforeFooter
  };
};

const mapDispatchToProps = dispatch => ({
  requestGoogleBeforeFooter: () => dispatch(requestGoogleBeforeFooter())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Section2);
